@import (less) "color.less";

@md-width: 350px;
@sm-width: 200px;
@lg-width: 500px; 
@min-heigth: 50px;

.top,.bottom, .right, .top-right, .bottom-right, .top-left, .bottom-left{
  .icon-position {
    position:absolute;
    top: 5px;
    right: 5px;
    color:goldenrod;
    font-weight:bold;
  }
}

.left{
  .icon-position {
    position:absolute;
    top: 5px;
    left: 5px;
    color:goldenrod;
    font-weight:bold;
  }
}

.nbp-tooltip, .nbp-popover {
    position: relative;
    display: inline-block;   
}

.clickable{
  cursor: pointer;
}


/* Tooltip text */

.nbp-tooltip .tooltip-content , .nbp-popover .popover-content{
    visibility: hidden;
    background-color: @darkest_gray;
    color: #fff; 
    padding: 5px 0; 
    position: absolute;
    z-index: 1;
    display: inline-block !important; 
    .tooltip-text{
      text-align: center;
    }
    
    .popover-text{
      text-align: center;
    }

    &.md{
      width: @md-width;
    }
    &.sm{
      width: @sm-width;
    }
    &.lg{
      width: @lg-width;
    }
}

.nbp-tooltip .tooltip-content.right, .nbp-popover .popover-content.right {
    top: 50% !important;
    left: 100% !important;
    margin-left:10px;
    transform: translateY(-50%);
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 100%;
        transform: translateY(-50%); 
        border-top-width: 10px;
        border-right-width: 9px;
        border-bottom-width: 10px;
        border-left-width: 9px;
        border-style: solid;
        border-color: transparent @darkest_gray transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.left , .nbp-popover .popover-content.left {
    top: 50%;
    right: 100%;
    margin-right:10px;
    transform: translateY(-50%);
    &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translateY(-50%);
        border-top-width: 10px;
        border-right-width: 9px;
        border-bottom-width: 10px;
        border-left-width: 9px;
        border-style: solid;
        border-color: transparent transparent transparent @darkest_gray;
    }
}

.nbp-tooltip .tooltip-content.top , .nbp-popover .popover-content.top {
    bottom: 100%;
    left: 50%;
    margin-bottom:13px;
    transform: translateX(-50%); 
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 50%; 
        transform: translateX(-50%);
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom , .nbp-popover .popover-content.bottom {
    top: 100%;
    left: 50%;
    margin-top:15px;
    transform: translateX(-50%); 
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        left: 50%;
        transform: translateX(-50%); 
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip .tooltip-content.top-left , .nbp-popover .popover-content.top-left {
    bottom: 100%; 
    left: 0;
    margin-bottom: 15px;
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        left: 5%; 
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.top-right , .nbp-popover .popover-content.top-right {
    bottom: 100%; 
    right: 0;
    margin-bottom:15px; 
    &::after {
        content: "";
        position: absolute;
        top: 100%;
        right: 5%; 
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: @darkest_gray transparent transparent transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom-left , .nbp-popover .popover-content.bottom-left {
    top: 100%;
    left: 0;
    margin-top:15px;
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        left: 5%;
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip .tooltip-content.bottom-right , .nbp-popover .popover-content.bottom-right {
    top: 100%;
    right: 0;
    margin-top:15px;
    &::after {
        content: "";
        position: absolute;
        bottom: 100%;
        right: 5%;
        border-top-width: 10px;
        border-right-width: 12px;
        border-bottom-width: 10px;
        border-left-width: 12px;
        border-style: solid;
        border-color: transparent transparent @darkest_gray transparent;
    }
}

.nbp-tooltip:hover .tooltip-content , .nbp-popover .popover-content.opened {
    visibility: visible;
}
